<template>
  <div>
    <div v-if="loading" style="margin-top: 24%;margin-left: 55%;">
      <a-spin size="large"/>
    </div>
    <div v-else style="margin-left: 6%">
      <a-descriptions bordered title="咨询详情" :column="{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }">
        <a-descriptions-item label="标题" span="3">
          {{ description.title }}
        </a-descriptions-item>
        <a-descriptions-item label="姓名">
          {{ description.name }}
        </a-descriptions-item>
        <a-descriptions-item label="性别">
          {{ description.sex }}
        </a-descriptions-item>
        <a-descriptions-item label="电话">
          {{ description.phone }}
        </a-descriptions-item>
        <a-descriptions-item label="地址" span="2">
          {{ region }}
        </a-descriptions-item>
        <a-descriptions-item label="咨询时间">
          {{ description.createTime }}
        </a-descriptions-item>
        <a-descriptions-item label="案件详情" span="3">
          {{ description.desc }}
        </a-descriptions-item>
        <a-descriptions-item label="咨询诉求" span="3">
          {{ description.demand }}
        </a-descriptions-item>
        <a-descriptions-item label="附件" span="3">
          <ul v-for="item in description.file" :key="item.id"
              style="list-style-type:none;margin-left: -3%;margin-bottom: 1%;">
            <li>
              <a href="" @click.prevent="downloadFile(item)">
                <a-icon type="paper-clip"/>
                {{ item.name }}
              </a>
            </li>
          </ul>
        </a-descriptions-item>
      </a-descriptions>

      <div style="margin-top: 5%;">
        <a-divider>回复</a-divider>
      </div>


      <a-list
          class="comment-list"
          item-layout="horizontal"
          :data-source="comments"
      >
        <a-list-item slot="renderItem" slot-scope="item, index">
          <a-comment :author="item.name" :avatar="item.avatar">

            <div slot="content">
              <p>
                {{ item.content }}
              </p>
              <ul v-for="file in item.file" :key="file.id"
                  style="list-style-type:none; margin-left: -3%;margin-bottom: 1%;">
                <li>
                  <a href="#" @click.prevent="downloadFile(file)">
                    <a-icon type="paper-clip"/>
                    <span> {{ file.name }}</span>
                  </a>
                </li>
              </ul>

            </div>
            <a-tooltip slot="datetime" :title="moment(item.date).format('YYYY-MM-DD')">
              <span>{{ moment(item.date, 'YYYY-MM-DD').fromNow() }}</span>
            </a-tooltip>
          </a-comment>
        </a-list-item>
      </a-list>
    </div>
  </div>

</template>

<script>
  import moment from 'moment';
  import 'moment/locale/zh-cn';
  import request from "@/utils/request";

  export default {
    name: 'ConsultationDetail',
    data() {
      return {
        description: {
          title: '',
          name: '',
          sex: '',
          phone: '',
          region: ["安徽省", "合肥市", "瑶海区", "明光路街道", "xx小区"],
          createTime: '',
          desc: '',
          demand: '',
          file: [
            {
              id: '001',
              name: '文件1',
              url: '',
              size: ''
            },
            {
              id: '002',
              name: '文件2',
              url: '',
              size: ''
            }
          ]

        },
        //回复
        comments: [{
          avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
          name: 'Han Solo',
          content: '你说得对，但是Apex...',
          date: '2022-08-17',
          file: [
            {
              id: '001',
              name: '文件1',
              url: '',
              size: ''
            },
            {
              id: '002',
              name: '文件2',
              url: '',
              size: ''
            }
          ]
        }],
        moment,
        loading: false
      };
    },
    methods: {
      getDetail() {
        this.loading = true;

        request({
          method: 'get',
          url: '/law/response',
          params: {
            id: this.$route.query.id
          }
        }).then(res => {

          if (res.code === 1) {
            this.description = res.data.detail;
            this.comments = [res.data.comments];
          } else {
            this.$message.error(res.msg);
          }
          this.loading = false;

        }).catch(error => {
          this.$message.error('服务器错误！');
          console.log(error);
          this.loading = false;
        })

      },
      downloadFile(item) {
        // console.log(item);

        const {id, name, url} = item;

        request({
          method: 'get',
          url: '/law/download',
          params: {id: id},
        }).then(res => {

          console.log(res.code);
          
          if (res.code === 1) {
            const link = document.createElement('a');
            let blob = new Blob([res.data]);
            link.download = name;
            link.href = URL.createObjectURL(blob);
            link.target = '_blank';
            link.style.display = 'none';
            document.body.appendChild(link);
            link.click();

            URL.revokeObjectURL(link.href);
            document.body.removeChild(link);
          }else {
            this.$message.error(res.msg);
          }

        }).catch(error => {
          this.$message.error('服务器错误！');
          console.log(error);
        })

      }
    },
    computed: {
      region() {
        let res = '';
        for (let i = 0; i < this.description.region.length; i++) {
          res += this.description.region[i];
        }
        return res;
      }
    },
    activated() {
      this.getDetail();
    }

  }
</script>

<style scoped>
  .icon {
    display: inline-block;
    vertical-align: baseline;
  }

  a {
    color: rgba(0, 0, 0, 0.65);
  }
</style>